.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.form{
    background: #F3FBF7;
    padding: 30px;
    width: 35%;
    box-shadow: 0px 0px 15px #86A273;
}

.success{
    background: blue;
    color: white;
    height: 50px;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.form input{
    width: 100%;
    height: 50px;
    outline: none;
    border: none;
    border: 2px solid #F6F5F9;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 30px;
    font-size: 14px;
}

.form input:focus{
    border: 2px solid #98AFC7;
}

.form span{
    font-size: 12px;
    color: red;
}

.form-action{
    margin-top: 30px;
}

.form-action button{
    width: 100%;
    height: 50px;
    font-size: 16px;
    outline: none;
    border: none;
    color: white;
    background: #74C56B;
    cursor: pointer;
}

/* Responsiveness */
/* For Tab and bigger screens */
@media (max-width:1024px){
    .form{
        width: 60%;
    }
}

/* For Mobile */
@media (max-width:600px){
    .form{
        width: 80%;
    }
}